	                   “韩国电池企业在华遭排挤，中方报复萨德力度再升级”，韩国《中央日报》2日报道称，汽车业界传来消息说，中国相关部门去年底发布新能源电动汽车补助名录，将韩国三星和LG的相关电池产品排除在外。韩国《朝鲜日报》援引韩国驻华大使馆的消息说，中方当时共宣布498个车型作为补贴发放对象，但下午突然将其中的5款车去除。进入名单后又被去掉的车型都是安装韩国产电池的汽车。《首尔新闻》评论称，这实际上意味着韩国企业相关产品已经难以进入中国市场销售。
	                 </p>
	                </section>
	                <section>
	                    <p>
	                        “从年初开始中国就采取报复措施，今年韩中关系很有可能继续亮红灯”，韩国CBS电台2日评论说，不仅韩国汽车电池企业被排除在中国市场之外，中国有关部门此前还不批准韩国航空公司在今年1月运航包机，这让希望借中国春运东风大赚一笔的韩国航空公司大失所望。这些措施可谓“前所未有”。有分析认为，由于中国已将“萨德”视作与领土主权相关的重大利益关切并持续强烈反对，未来如果“萨德”真的部署完毕，中国的反制措施可能更加强硬。
	                    </p>
	                </section>
	                <section>
	                    <p>
	                        就中国疑似为反制“萨德”入韩采取的一系列措施，韩国外交部长官尹炳世2日向记者表示，政府会在必要时采取措施。他透露说，目前包括外交部在内，政府内部正在进行必要的讨论。汇总意见、制定措施需要走一定的程序，该程序已有较大进展。韩联社称，政府相关部门据称正努力寻求多种应对方案，但观察人士认为，韩方可采取的应对措施有限。
	                    </p>
	                </section>
	                <section>
	                    <p>
