	                    </p>
	                </section>
	                <section>
	                    <p>
	                        从4日开始，韩国最大在野党共同民主党所属8名国会议员将开始为期3天的中国之旅。《韩国日报》2日报道称，他们此行将拜会中国外交部、商务部、中联部等部门。该党国会议员宋永吉说，代表团希望通过此行缓解“韩流”在华发展受限、经济交流合作受阻的情况。他表示，前党首文在寅等几位潜在总统候选人已表明应将“萨德”入韩事宜交由下届政府重新研究的立场，代表团将向中方转达相关情况。
	                    </p>
