<template>
	<div  class="qqFace">
		<table border="0" cellspacing="0" cellpadding="0">
			<tr  v-for="r in 5">
				<td v-for="d in 15">
					<img :src="option.path+(d+(r-1)*15)+'.gif'"  @click="selected((d+(r-1)*15))">
				</td>
			</tr>
		</table>
	</div>
</template>
<script type="text/javascript">
import $ from  "jquery";
export  default{
	props:['option'],
	data(){
		return{

		}
	},
	methods:{

		selected(num){
			var  str = $("#"+this.option.assign).val()+'[em_'+num+']';
			$("#"+this.option.assign).val(str);
			$("#"+this.option.id).hide();
		}
	}
}
</script>
<style type="text/css">
	.qqFace{
		position:absolute;
		z-index:1000;
		top: 0;
	}
</style>


